"use client";

import { useRef, useEffect } from "react";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { ArrowUp } from "lucide-react";

// LawGPT logo SVG
const LawGPTLogo = () => (
  <svg width="40" height="40" viewBox="0 0 40 40" fill="none" xmlns="http://www.w3.org/2000/svg">
    <g>
      <circle cx="20" cy="20" r="20" fill="#1B3C3B"/>
      <rect x="11" y="18" width="18" height="4" rx="2" fill="#2DD4BF"/>
      <rect x="18" y="11" width="4" height="18" rx="2" fill="#2DD4BF"/>
    </g>
  </svg>
);

// Animated placeholder card
const AnimatedCard = ({ direction, style, arrow, className }: any) => (
  <div
    className={`absolute ${className} flex flex-col items-center justify-center pointer-events-none select-none`}
    style={style}
  >
    <div className={`bg-[#262626] rounded-3xl w-[120px] h-[220px] flex items-center justify-center relative overflow-hidden`}
      style={{
        clipPath: direction === "left"
          ? "polygon(0 0, 100% 0, 100% 100%, 0 80%)"
          : "polygon(0 0, 100% 20%, 100% 100%, 0 100%)"
      }}
    >
      {/* Placeholder for image */}
      <span className="text-4xl text-white/20">üñºÔ∏è</span>
    </div>
    {/* Arrow */}
    <svg
      width="48"
      height="48"
      viewBox="0 0 48 48"
      fill="none"
      xmlns="http://www.w3.org/2000/svg"
      className="mt-2"
    >
      <path
        d={arrow === "up"
          ? "M24 40V8M24 8l-8 8M24 8l8 8"
          : "M24 8v32M24 40l-8-8M24 40l8-8"}
        stroke="#fff"
        strokeWidth="3"
        strokeLinecap="round"
        strokeLinejoin="round"
      />
    </svg>
  </div>
);

export default function LawGPTPage() {
  // Animate the cards up/down
  const leftRef = useRef<HTMLDivElement>(null);
  const rightRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    let frame = 0;
    let req: number;
    function animate() {
      frame++;
      if (leftRef.current) {
        leftRef.current.style.transform = `translateY(${Math.sin(frame/60)*16}px)`;
      }
      if (rightRef.current) {
        rightRef.current.style.transform = `translateY(${Math.cos(frame/60)*16}px)`;
      }
      req = requestAnimationFrame(animate);
    }
    animate();
    return () => cancelAnimationFrame(req);
  }, []);

  return (
    <div className="relative min-h-screen bg-[#111] flex flex-col items-center justify-center overflow-hidden">
      {/* Animated left card */}
      <div ref={leftRef} className="hidden md:block" style={{ left: 0, top: 80, zIndex: 1, position: 'fixed' }}>
        <AnimatedCard direction="left" arrow="up" className="-left-12 top-32" />
      </div>
      {/* Animated right card */}
      <div ref={rightRef} className="hidden md:block" style={{ right: 0, top: 120, zIndex: 1, position: 'fixed' }}>
        <AnimatedCard direction="right" arrow="down" className="-right-12 top-64" />
      </div>

      {/* Main content */}
      <div className="flex flex-col items-center justify-center w-full z-10 mt-24">
        <div className="flex flex-col items-center mb-8">
          <LawGPTLogo />
          <span className="text-lg text-[#2dd4bf] font-medium mt-2">LawGPT</span>
        </div>
        <h1 className="text-4xl md:text-5xl font-bold text-white mb-8 text-center">What can i help with</h1>
        <form className="w-full max-w-2xl flex flex-col items-center">
          <div className="relative w-full">
            <input
              type="text"
              placeholder="Ask me anything about law"
              className="w-full h-20 rounded-2xl bg-[#222] text-white text-lg px-8 pr-20 placeholder:text-gray-400 outline-none border-none shadow-lg"
            />
            <Button
              type="submit"
              className="absolute right-4 top-1/2 -translate-y-1/2 rounded-full bg-white text-black w-12 h-12 flex items-center justify-center shadow-lg hover:bg-[#2dd4bf] hover:text-white transition-colors"
            >
              <ArrowUp className="w-6 h-6" />
            </Button>
          </div>
        </form>
      </div>
    </div>
  );
}
